.bg{
  background-image:url("../../Assets/bgg.gif");
    height: 125vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
   
}
 .btn{
  display: block;
  text-align:center;
  margin-left:35%;
  width:30%;
  height: 40px;
  line-height: 40px;
  font-size: 18px;
  font-family: sans-serif;
  text-decoration: none;
  color: #fff;
  letter-spacing: 2px;
  text-align: center;
  position: relative;
  transition: all .35s;
}

.btn span{
  position: relative;
  z-index: 100;
}

.btn:after{
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  background: #ff003b;
  transition: all .35s;
}

.btn:hover{
  color: #fff;
}

.btn:hover:after{
  width: 100%;
}
